* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.square {
  background: #fff;
  border: 1px solid #999999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 200%;
  height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  margin-left: -1px;
  margin-bottom: -1px;
  padding: 0;
  text-align: center;
  width: 60px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 15px;
  color: darkolivegreen;
}
.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.class-for-first-player {
  color: blue;
}

.class-for-second-player {
  color: brown;
}

.class-for-winner {
  background: grey;
  color: white;
  position: relative;
  /* font-family: Impact, sans-serif;
  font-size: 30px; */
  letter-spacing: 2px;
  background: linear-gradient(45deg, rgb(80, 80, 204), rgb(189, 100, 100));
  background-repeat: no-repeat;
  background-size: 100%;
  animation: animate 5s infinite;
}

.setting {
  margin: 0px 20px 20px 40px;
  color: lightslategrey;
  display: block; 
  font-weight: bold; 
}

.setting-label {
  color: lightslategrey;
  display: block; 
  font-weight: normal; 
  margin-bottom: 3px; 
}

.setting-field {
  line-height: normal;
  width: 100%;
  padding: 6px 10px;
  margin: 0 0 20px 0;
  box-sizing: border-box;
  border: 2px solid lightslategrey;
  border-radius: 4px;
  background-color: #fafcfb;
  color: lightslategrey;
  font-weight: bold;
  outline: none; 
}

.setting-field-disabled {
  line-height: normal;
  width: 100%;
  padding: 6px 10px;
  margin: 0 0 20px 0;
  box-sizing: border-box;
  border: 2px solid lightslategrey;
  border-radius: 4px;
  background-color: #fafcfb;
  color: lightslategrey;
  font-weight: bold;
  outline: none;
}

.normal-button {
  background-color: lightslategrey;  
  color: white; 
  padding: 10px 20px; 
  border: none;
  border-radius: 4px; 
  cursor: pointer; 
  font-weight: bold;
}

.small-button {
  background-color: lightslategrey;  
  color: white; 
  padding: 5px 5px; 
  border: none;
  border-radius: 4px; 
  cursor: pointer; 
  font-weight: bold;
}